.wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  display: flex;
  column-gap: 40px;
  align-items: center;
  border-radius: 20px;
  padding: 20px;
  width: fit-content;
  box-shadow: 2px 2px 16px rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  transform: translate(-50%, -50%);
}